getLocation () {
let vm = this
AMap.plugin('AMap.Geolocation', function () {
var geolocation = new AMap.Geolocation({
enableHighAccuracy: true, // 是否使用高精度定位，默认:true
timeout: 10000, // 超过10秒后停止定位，默认：5s
buttonPosition: 'RB', // 定位按钮的停靠位置
buttonOffset: new AMap.Pixel(10, 20), // 定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
zoomToAccuracy: true 
})















geolocation.getCurrentPosition(function (status, result) {
if (status === 'complete') {
let lss = result.addressComponent
vm.location.province = lss.province
vm.location.city = lss.city
vm.location.district = lss.district
console.log(result)
vm.cancelTo.txt = lss.province + lss.city + lss.district
vm.cancelTo.show = true
} else {
vm.cancelTo.txt = result.message || '定位失败'
vm.cancelTo.show = true
}
})
})
}
},
mounted () {












this.getLocation()



}
